        "TEXT_BUILDER",
        "PASSWORD",
        "CURRENCY",
        "FILE_UPLOAD",
      ].includes(this.fieldType);
    },

    showContentRule() {
      return ["SHORT_TEXT", "LONG_TEXT", "NUMBER", "CURRENCY"].includes(
        this.fieldType
      );
    },

    _contentRules() {
      return this.contentRules.filter((option) =>
        option.for.includes(this.fieldType)
