    width: 100%;
    border-collapse: collapse;

    tr {
      height: 48px;
    }

    th:not(.action) {
      padding: 4px 8px;
      text-align: left;
      font-weight: 500;
      text-transform: capitalize;

      &.small {
        width: 80px;
      }

      &.medium {
        width: 150px;
      }

      &.large {
        width: 210px;
      }
    }

    th,
    td {
      border: 1px solid var(--divider-color);
      &.action {
