<template>
  <div id="form-builder">
    <ReportsFields :active-panel-type="activePanelType" />

    <Reports
      class="col"
      :form="form"
      :active-panel-type="activePanelType"
      @switch-panel-type="switchPanelType"
      @update-panels="updatePanels"
      @update-secondary-panels="updateSecondaryPanels"
      @settings="showSettings"
      @save="saveForm"
    />
  </div>
</template>

<script>
import ReportsFields from "./components/ReportsFields.vue";
import Reports from "./components/reports/Reports.vue";

export default {
  name: "ReportsBuilder",

  components: { ReportsFields, Reports },

  props: {
    form: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      activePanelType: "PRIMARY_PANELS",
    };
  },

  methods: {
    switchPanelType(type) {
      this.activePanelType = type;
    },

    updatePanels(panels) {
      this.$emit("update-panels", panels);
    },

    updateSecondaryPanels(panels) {
      this.$emit("update-secondary-panels", panels);
    },

    showSettings() {
      this.$emit("settings");
    },

    saveForm() {
      this.$emit("save");
    },
  },
};
</script>

<style lang="scss" scoped>
#form-builder {
  min-height: calc(100vh - 68px);
  background-color: var(--body-bg-color);
  display: flex;
}
</style>
