<!-- eslint-disable vue/no-v-html -->

<template>
  <div id="formula-builder">
    <!-- label -->

    <FormFieldLabel label="formula" is-mandatory />

    <!-- ... -->

    <!-- wrapper -->

    <div class="wrapper" :class="{ error: error }">
      <!-- tokens -->

      <div class="tokens">
        <div
          v-for="token in tokens"
          :key="token.id"
          class="token"
          @click="addToken(token.name, token.value)"
        >
          <span v-html="token.code"></span>
        </div>
      </div>

      <!-- ... -->

      <BaseSeparator is-vertical class="q-mx-md" />

      <!-- formula -->

      <div class="formula">
        <template v-for="(token, idx) in formula">
          <!-- field -->

          <div
            v-if="token.name === 'FIELD'"
            :key="token.id"
            class="input field"
          >
            <div class="full-width">
              <div class="row items-center">
                <div class="col label ellipsis">
                  <span v-if="token.label">{{ token.label }}</span>
                  <span v-else class="placeholder">Select</span>
                </div>
                <BaseIcon name="eva-chevron-down" inherit-color class="icon" />
              </div>

              <q-menu
                fit
                auto-close
                transition-show="scale"
                transition-hide="scale"
              >
                <ListItem
                  v-for="field in numericFields"
                  :key="field.id"
                  :is-selected="token.value === field.value"
                  :label="field.label"
                  class="text-capitalize"
                  @click="
                    token.label = field.label;
                    token.value = field.value;
                  "
                />
              </q-menu>
            </div>

            <BaseIcon
              name="eva-close"
              color="red"
              class="delete-icon"
              @click="formula.splice(idx, 1)"
            />
          </div>

          <!-- ... -->

          <!-- number -->

          <div
            v-else-if="token.name === 'NUMBER'"
            :key="token.id"
            class="input number"
          >
            <input
              :value="token.value"
              type="number"
              @input="
                (e) => {
                  token.label = e.target.value;
                  token.value = e.target.value;
                }
              "
            />

            <BaseIcon
              name="eva-close"
              color="red"
              class="delete-icon"
              @click="formula.splice(idx, 1)"
            />
          </div>

          <!-- ... -->

          <!-- other tokens -->

          <div v-else :key="token.id" class="token">
            <span v-html="getHTMLCode(token.name)"></span>

            <BaseIcon
              name="eva-close"
              color="red"
              class="delete-icon"
              @click="formula.splice(idx, 1)"
            />
          </div>

          <!-- ... -->
        </template>
      </div>

      <!-- ... -->
    </div>

    <!-- ... -->

    <!-- error -->

    <FormFieldError v-if="error" :error="error" />

    <!-- ... -->
  </div>
</template>

<script>
import FormFieldLabel from "@/components/common/form/FormFieldLabel.vue";
import FormFieldError from "@/components/common/form/FormFieldError.vue";
import ListItem from "@/components/common/ListItem.vue";
import { cloneDeep, isEqual } from "lodash-es";

export default {
  name: "FormulaBuilder",

  components: {
    FormFieldLabel,
    FormFieldError,
    ListItem,
  },

  props: {
    value: {
      type: Array,
      required: true,
    },

    error: {
      type: String,
      default: "",
    },

    numericFields: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      tokens: [
        {
          id: this.$nano.id(),
          code: "Field",
          name: "FIELD",
          value: "",
        },
        {
          id: this.$nano.id(),
          code: "&#43;",
          name: "PLUS",
          value: "+",
        },
        {
          id: this.$nano.id(),
          code: "&#8722;",
          name: "MINUS",
          value: "-",
        },
        {
          id: this.$nano.id(),
          code: "&#215;",
          name: "MULTIPLY",
          value: "*",
        },
        {
          id: this.$nano.id(),
          code: "&#247;",
          name: "DIVIDE",
          value: "/",
        },
        {
          id: this.$nano.id(),
          code: "&#40;",
          name: "LEFT_PARANTHESES",
          value: "(",
        },
        {
          id: this.$nano.id(),
          code: "&#41;",
          name: "RIGHT_PARANTHESES",
          value: ")",
        },
        {
          id: this.$nano.id(),
          code: "Number",
          name: "NUMBER",
          value: "",
        },
      ],
      formula: [],
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler() {
        if (isEqual(this.value, this.formula)) {
          return;
        }

        this.formula = cloneDeep(this.value);
      },
    },

    formula: {
      deep: true,
      handler() {
        this.$emit("input", this.formula);
      },
    },
  },

  methods: {
    addToken(name, label) {
      this.formula.push({
        id: this.$nano.id(),
        name: name,
        label: label,
        value: "",
      });
    },

    getHTMLCode(tokenName) {
      return this.tokens.find((token) => token.name === tokenName).code;
    },
  },
};
</script>

