      border-radius: 36px;
      font-size: 24px;
      font-weight: 500;
      color: var(--icon-color);
      user-select: none;
      position: relative;

      .delete-icon {
        display: none;
        position: absolute;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
        background-color: var(--component-bg-color);
        border-radius: 36px;
        cursor: pointer;
      }

      &:hover {
        .delete-icon {
          display: flex;
        }
      }
    }

    .tokens {
      min-width: 88px;
      width: 88px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 8px;

      .token {
        background-color: var(--body-bg-color-light);
        border-radius: 4px;

        &:hover {
          cursor: pointer;
          background-color: var(--hover-bg-color);
        }

        &:first-child,
        &:last-child {
          width: 100%;
