        height: 40px;
        padding: 8px;
        display: flex;
        align-items: center;
        transition: width 0.3s ease;

        .label {
          font-weight: 500;
          margin-right: 8px;
        }

        .icon {
          color: var(--icon-color-inverted);
        }

        .delete-icon {
          display: none;
          cursor: pointer;
        }

        &.field {
          // width: 120px;
          cursor: pointer;

          &:hover {
            // width: 140px;

            .delete-icon {
              display: inline-block;
            }
          }
        }

        &.number {
          width: 56px;

          &:hover {
            width: 76px;

            .delete-icon {
              display: inline-block;
            }
          }
        }
      }
    }
  }
}
</style>
